
#edit-profile-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
    .profile-picture {
    
      width:25vw;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      img {
        border-radius: 20%;
        width: 200px;
        height: 200px;
      }
      hr {
        width: 150px;
        margin-top: 5px;
      }
      span {
        margin-top: 10px;
        font-family: monospace;
        font-size: 0.5rem;
      }
      input[type="file"] {
        display: none;
        &:hover {
          cursor: pointer;
          width: 100px;
        }
      }

      label {
        padding: 10px;
        margin-top: 10px;
        background-color: #aaa;
        color: white;
        border-radius: 10px;
        cursor: pointer;
      }
      h6 {
        margin-top: 10px;
        font-family: monospace;
        font-size: 11px;
        font-weight: 600;
        color: #4e4e4e;
        text-align: center;
      }
    }
    form {
      border-radius: 15px;
      padding: 30px 30px 30px 0;
      display: flex;
      flex-direction: column;
      width: 40vw;
      gap: 10px;
      align-items: center;
      justify-content: center;
      input,
      textarea,
      select {
        width: 40vw;
        height: 5vh;
        padding: 15px;
        border-radius: 10px;
        border: none;
        box-shadow: 3px 6px 9px rgb(128, 124, 124);
      }
      input[type="submit"] {
        background-color: rgb(194, 191, 191);
        width: 15vw;
        color: aliceblue;
        font-family: monospace;
        font-size: 15px;
        font-weight: 700;
      }
    }
  }