#complaints-container {
  width: 100%;
  #complaints-list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    gap: 10px;
    cursor: pointer;
  }
  .list-load-more {
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: -30px;
    display: flex;
    justify-content: center;
    padding: 10px 10px;
    border-radius: 10px;
    font-family: monospace;
    font-size: 20px;
    border: none;
    box-shadow: 2px 4px 8px black;

    &:hover {
      cursor: pointer;
      background-color: #aaa;
      padding: 10px 20px;
      transition: 0.1s;
    }
    &:active {
      transform: scale(1.1);
      transition: 0.2s;
    }
  }
  .pages-items-count {
    position: absolute;
    left: 5px;
    bottom: 5px;
    font-family: monospace;
    font-size: 15px;
    font-weight: 700;
  }
}
