.login-container {
    width: 100vw;
    height: 100vh;
    background-color: rgba(128, 128, 128, 0.81);
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 5;
    h2 {
      font-family: monospace;
      margin-bottom: 20px;
    }
    form {
      background-color: #eee;
      border-radius: 15px;
      padding: 50px;
      display: flex;
      flex-direction: column;
      width: 70vw;
      height: 50vh;
      gap: 15px;
      align-items: center;
      justify-content: center;
      position: relative;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
     
      input{
        width: 50vw;
        height: 5vh;
        padding: 15px;
        border-radius: 10px;
        border: none;
        font-size: 17px;
        font-family: monospace;
        color: black;
        font-weight: 600;
        box-shadow: 3px 6px 9px rgb(128, 124, 124);
      }
      input[type="submit"] {
        background-color: rgb(255, 255, 255);
        border: 1px solid #16b6de;
        color: black;
        width: 15vw;
        font-family: monospace;
        font-size: 17px;
        font-weight: 700;
        &:hover {
          background-color: #ccc;
          transition: 0.3s;
          cursor: pointer;
        }
        &:active {
          transform: scale(1.2);
          transition: 0.3s;
        }
      }
  
    
    }
  }
  